.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  // max-width: 1200px;
  // overflow: 1px solid green;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}
